@import '../../sass/utils/mixin';

.menu__list {
    @include mq(mob) {
      display: flex;
      flex-direction: column;
      text-align: center;
      align-items: center;
      justify-content: center;
      width: 100%;
    }

  @include mq(tab) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 840px;
  }
}

.menu__list_link {
  width: 160px;
  display: flex;
  transform: none;
  align-items: center;
  justify-content: center;
  margin-bottom: 24px;
  border-radius: 4px;
  background-color: var(--eggShellWhite);
  box-shadow: 2px 2px 15px 4px rgba(107, 103, 102, 0.15);

  @include mq(desk) {
    width: auto;
    line-height: 1.38;
  }
}

.menu__list_link:last-child {
  margin-bottom: 0px;
}

.menu__list_item {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  font-family: 'Poppins Regular';
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    color: var(--richSoilBrown);

  @include mq(mob-only) {
    padding: 12px;
  }

  @include mq(tab) {
    align-items: center;
    padding: 8px 4px;
  }
}

.menu__list_item:not(:last-child) {
  @include mq(tab) {
      margin-right: 10px;
    }
 
}

.menu__list_item a:hover,
.menu__list_item a:focus {
    border-bottom: 5px solid var(--primaryTextColor);
    margin-bottom: -5px;
}

.navBar {
  display: flex;
  align-items: center;
  justify-content: center;

    @include mq(tab) {
    display: flex;
    align-items: center;
    justify-content: center;
    }
  }
  
  @media screen and (max-width: 768px) {
    .navBar .navigation {
      display: none;
    }
  }
  
  @media (min-width: 769px) {
    .navBar .navigation {
      display: flex;
    }
  
    .navBar .mobileNavigation {
      display: none;
    }
  }

.navBar .mobileNavigation .hamburgerMenu {
  cursor: pointer;
}

.hamburgerMenu {
  width: 42px;
  height: 42px;
  color: var(--primaryTextColor);
  cursor: pointer;
}

.themeMenu {
  width: 40px;
  height: 40px;
  cursor: pointer;

  @include mq(desk) {
    margin-right: 0px;
  }
}

.menuList__container {
  padding-bottom: 16px;
  width: 360px;
  position: absolute;
  top: 336px;
  left: 50%;
  transform: translate(-50%, -50%);

  @include mq(tab) {
    width: 840px;
    position: static;
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 25px;
    padding-right: 25px;
  }
}

.nav {
  display: flex;
  align-items: center;

  @include mq(tab) {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
  }
}

.bgShadow {
  position: fixed;
  top: 78px;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(39, 18, 12, 0.7);
  z-index: 9;
}